"use client";

import { useParams } from 'next/navigation';
import { useState } from 'react';

const CameraPage = () => {
  const { cameraName } = useParams();
  const [camera, setCamera] = useState<{ name: string; timestamp: string } | null>(null);

  return (
    <div className="container mx-auto p-4 bg-gray-900 text-white min-h-[90vh] rounded-xl shadow-lg">
      <div className="relative w-full h-[70vh] rounded-lg overflow-hidden">
        <div className="video-container">
          <video
            controls
            autoPlay
            className="video-player w-full h-full rounded-lg shadow-lg"
            src="http://160.22.122.122:8080/hls/live.m3u8"
          >
            Trình duyệt của bạn không hỗ trợ phát video này.
          </video>
        </div>
      </div>

      {camera && (
        <div className="mt-8">
          <h2 className="text-2xl font-semibold mb-2">Thông tin chi tiết:</h2>
          <p>Tên: {camera.name}</p>
          <p>Thời lượng: {camera.timestamp}</p>
        </div>
      )}
    </div>
  );
};

export default CameraPage;
